.dialogs {
    background-color: lightblue;
    color: blue;
    height: 200px;
    display: grid;
    grid-template-columns: 2fr 10fr;
}

.dialogsItems {
    padding: 10px;
}

.dialogsItems .active {
    color: gold;
}
.dialog *{
    text-decoration: none;
}

.dialog .activeLink{
    color: green;
}

.messages {
    padding: 10px;
}

